.bitcoin-news {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 15px;
  padding: 2rem;
  box-shadow: 25px 25px 25px rgba(0, 0, 0, 0.803);
  border-radius: 30px;
  background: rgb(255, 255, 255);
  background: rgba(255, 255, 255, 0.703);
}

.article {
  margin: 1rem 0;
}

.article h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.article p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.article a {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: #0077FF;
  color: #FFFFFF;
  text-decoration: none;
  border-radius: 40px;
}

.article a:hover {
  background-color: #005BCC;
}
