.bitcoin-price {
  max-width: 300px;
  margin: 0 auto;
  margin-top: 50px;
  padding: 2rem;
  background-color: #fdfdfd;
  box-shadow: 25px 25px 25px rgba(0, 0, 0, 0.803);
  border-radius: 30px;
  background: rgb(255, 255, 255);
  background: rgba(255, 255, 255, 0.703);
  text-align: center;
}

.bitcoin-price h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.bitcoin-price p {
  font-size: 1.5rem;
  text-align: center;
  margin-top: 0;
}

.coin {
  font-size: 200px;
  width: 0.1em;
  height: 1em;
  background: linear-gradient(#faa504, #141001);
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation: rotate_4001510 7s infinite linear;
  transform-style: preserve-3d;
}

.coin .side, .coin:before, .coin:after {
  content: "";
  position: absolute;
  width: 1em;
  height: 1em;
  overflow: hidden;
  border-radius: 50%;
  right: -0.4em;
  text-align: center;
  line-height: 1;
  transform: rotateY(-90deg);
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.coin .tails, .coin:after {
  left: -0.4em;
  transform: rotateY(90deg);
}

.coin:before, .coin:after {
  background: linear-gradient(#faa504, #141001);
  backface-visibility: hidden;
  transform: rotateY(90deg);
}

.coin:after {
  transform: rotateY(-90deg);
}

@keyframes rotate_4001510 {
  100% {
    transform: rotateY(360deg);
  }
}

.svg_back {
  transform: scaleX(-1);
}


