.login-form {
  max-width: 400px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 2rem;
  background-color: #fdfdfd;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  background: rgb(255, 255, 255);
  background: rgba(255, 255, 255, 0.703);
  text-align: center;
  }
  
  .login-form h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  }
  
  .login-form label {
  display: block;
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  }
  
  .login-form input[type="email"],
  .login-form input[type="password"] {
  display: block;
  width: 70%;
  margin-left: 50px;
  padding: 0.5rem;
  font-size: 1rem;
  border-color: black;
  border-radius: 5px;
  margin-bottom: 1rem;
  }
  
  .login-form button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  }
  
  .login-form button[type="submit"]:hover {
  background-color: #0069d9;
  }
  
  .login-form p {
  font-size: 1rem;
  text-align: center;
  margin-top: 0;
  }